header {
    margin: 1.5rem auto;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 1.15rem;
    user-select: none;
    nav .menu {
        cursor: pointer;
        font-size: 1.3rem;
    }
    aside {
        width: 33.3333%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }
    ul {
        display: flex;
        li {
            font-size: 0.9rem;
            color: gray;
            list-style: none;
            margin: 0 1.15rem;
            transition: .15s ease-in-out;
            cursor: pointer;
            &:hover {
                color: black;
                font-weight: 500;
            }
        }
    }
    .header-logo {
        color: rgb(40, 40, 40);
        font-size: 1.8rem;
        font-weight: 900;
        span {
            font-size: 2rem;
            color: rgb(255, 0, 0);
            font-family: "Whisper", cursive;
        }
    }
    .icons {
        .icon {
            margin: 0 .85rem;
            cursor: pointer;
        }
    }
}