.categories-mainPage {
    margin: 12.5vh auto 5vh;
    h1 {
        user-select: none;
        text-align: center;
        font-size: 4.25rem;
        color: rgb(35, 35, 35);
        font-weight: 500;
        letter-spacing: 8px;
        word-spacing: 10px;
        margin: 5vh 0;
    }
    .categories-tiles {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 95vw;
        margin: 0 auto;
        .photo-tile {
            background-image: url('https://images.unsplash.com/photo-1533090161767-e6ffed986c88?q=80&w=2069&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
            width: 47.5%;
            height: 50vh;
            margin: 1.25vh .75vw;
            background-size: cover;
            background-position: bottom;
            position: relative;
            .tile-text {
                color: rgb(25, 25, 25);
                position: absolute;
                left: 11%;
                top: 17.5%;
                font-size: 1.1rem;
                user-select: none;
                .tile-name {
                    font-size: 1.175rem;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    letter-spacing: 0.75px;
                    span {
                        margin: 0 .5rem;
                        letter-spacing: -1px;
                    }
                }
                .tile-description {
                    font-size: 2rem;
                    margin: 2.25rem 0;
                    font-weight: 500;
                    letter-spacing: 3.5px;
                    word-spacing: 4px;
                }
                .tile-text-button {
                    letter-spacing: 1.25px;
                    word-spacing: 2px;
                    cursor: pointer;
                    padding-bottom: 0.3rem;
                    display: inline-block;
                    width: auto;
                    font-weight: 500;
                    border-bottom: 1px solid rgba(0, 0, 0, 1);
                }
            }
            &:nth-of-type(2) {
                background-image: url('https://images.unsplash.com/photo-1494438639946-1ebd1d20bf85?q=80&w=2067&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
                height: 70vh;
            }
            &:nth-of-type(3) {
                background-image: url('https://images.unsplash.com/photo-1540574163026-643ea20ade25?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
                height: 70vh;
                margin-top: -18.65vh;
            }
            &:nth-of-type(4) {
                background-image: url('https://images.unsplash.com/photo-1502622796232-e88458466c33?q=80&w=1932&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
                height: 50vh;
                background-position: left;
            }
        }
    }
}