.home-section {
    margin-top: 5vh;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    h1 {
        position: absolute;
        z-index: 10;
        top: 57.5%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 8rem;
        color: rgb(30, 30, 30);
        font-weight: 700;
        letter-spacing: 10px;
        word-spacing: 12px;
    }
    .home-image1 {
        margin-left: 5vw;
        background-image: url('https://images.unsplash.com/photo-1617214922084-5db8d3c3df5a?q=80&w=1927&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
        height: 72.5vh;
        width: 27.5vw;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: left;
        position: relative;
        .description {
            width: 100%;
            left: 0;
            bottom: 5%;
            padding: 1.5rem 3rem;
            letter-spacing: 1.5px;
            word-spacing: 3px;
            font-weight: 500;
        }
    }
    .home-image2 {
        background-image: url('https://images.unsplash.com/photo-1567225557594-88d73e55f2cb?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
        height: 90vh;
        width: 50vw;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
        .description {
            left: 0;
            bottom: 0;
            padding: 2rem 6rem 2rem 1.75rem;
            letter-spacing: 1.5px;
            word-spacing: 3px;
            font-weight: 500;
        }
    }
    aside article {
        background-color: rgb(255, 255, 255);
        position: absolute;
    }
    .description .price {
        margin-top: 0.3rem;
        letter-spacing: 0.5px;
        word-spacing: 0;
        font-weight: normal;
        color: gray;
    }
    .home-decorations {
        display: flex;
        flex-direction: column;
        position: absolute;
        left: 0;
        margin-left: 1.5rem;
        span {
            font-size: 2.75rem;
            font-weight: bold;
            color: rgb(0, 0, 0);
            user-select: none;
        }
    }
    .social-icons-wrapper {
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 0;
        .social-icon {
            margin: 0.85rem 1.5rem;
            color: rgb(20, 20, 20);
            font-size: 2rem;
            cursor: pointer;
        }
    }
}