.featuredProducts {
    margin: 12.5vh auto 5vh;
    text-align: center;
    h1 {
        user-select: none;
        font-size: 4.25rem;
        color: rgb(35, 35, 35);
        font-weight: 500;
        letter-spacing: 8px;
        word-spacing: 10px;
    }
    .description {
        margin: 2.5vh 0;
        color: rgb(50, 50, 50);
        font-size: 1.1rem;
    }
    .categories-container {
        display: flex;
        flex-direction: row;
        width: 35vw;
        margin: 5vh auto;
        .category-item {
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            user-select: none;
            font-weight: 500;
            cursor: pointer;
            font-size: 0.9rem;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 3rem;
            width: 25%;
            color: rgb(50, 50, 50);
            background-color: rgb(210, 210, 210);
            position: relative;
        }
        .category-selected {
            color: #fff;
            background-color: rgb(40, 40, 40);
            &::after {
                content: '';
                position: absolute;
                bottom: -8px;
                left: 50%;
                transform: translateX(-50%);
                width: 0;
                height: 0;
                border-left: 14px solid transparent;
                border-right: 14px solid transparent;
                border-top: 8px solid rgb(40, 40, 40);
            }
        }
    }
    .show-more {
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 0.95rem;
        color: rgb(90, 90, 90);
        margin: 0 auto;
        border-radius: 50%;
        border: 1px solid rgb(175, 175, 175);
        width: 8.5rem;
        height: 8.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
}

.products-cards-wrapper {
    margin: 0 auto 5vh;
    width: 80vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    .product-card {
        margin: 1.5vh 0;
        background-color: rgb(255, 255, 255);
        text-align: left;
        width: 22.5%;
        .product-card-image {
            background-color: rgb(240, 240, 240);
            width: 100%;
            height: 350px;
            background-size: 82.5%;
            background-repeat: no-repeat;
            background-position: center;
            position: relative;
            span {
                cursor: pointer;
                position: absolute;
                top: 5%;
                right: 7.5%;
                font-size: 1.7rem;
                color: rgb(255, 255, 255);
                background-color: #3b3b3b;
                display: none;
                border-radius: 50%;
                padding: 5px;
            }
            &:hover span {
                display: flex;
            }
        }
        .product-card-description {
            width: 90%;
            margin: 0 auto;
            font-size: 0.925rem;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .product-name {
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-weight: 500;
                margin: 0.5rem 0 0.25rem;
            }
            .product-price {
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                text-transform: uppercase;
                color: rgb(90, 90, 90);
            }
            .product-card-icon {
                background-color: rgb(60, 60, 60);
                color: white;
                cursor: pointer;
                border-radius: 50%;
                width: 2rem;
                height: 2rem;
                padding: 4.5px;
            }
        }
    }
}

$product-images: ( 'https://imagebank.rosendahl.com/cdn/xZdg2t/Kahler-Omaggio-Circulare-Vase-Anthracite-grey-690149-xZdg2t.png?d=6838', 'https://www.coco-mat.com/international/sites/default/files/styles/product_spotlight_589x410_/public/2023-05/spotlight_accessories_lady_M_basket_589x411_green.png?itok=o9mzY8oQ', 'https://fondu.store/wp-content/uploads/2021/10/DONICZKI_bez_kwiaty-1.png', 'https://snoozerpetproducts.com/wp-content/uploads/2019/05/olive-24.png', 'https://9design.pl/eng_pl_Loft-Decora-Stol-Restauracyjny-ROUND-FLAT-90cr-41339_3.png', 'https://media.bouclair.com/image/upload/w_auto,q_auto,dpr_auto/products/images/highres/9404688', 'https://thermos.com/cdn/shop/products/SK1000MDB_PRES_1000px.png?v=1624023271', 'https://vintagecuisine.pl/cdn/shop/products/blackgroup2_1000x.png?v=1687183598');
@for $i from 1 through 8 {
    .product-card:nth-of-type(#{$i}) .product-card-image {
        background-image: url(nth($product-images, $i));
    }
}

.product-card:nth-of-type(1) .product-card-image {
    background-size: 75%;
}

.product-card:nth-of-type(2) .product-card-image {
    background-size: 115%;
}