.informations-section {
    margin: 12.5vh auto;
    width: 92.5vw;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .informations-description-wrapper {
        width: 40%;
        .informations-section-heading {
            font-size: 4.25rem;
            color: rgb(30, 30, 30);
            font-weight: 500;
            letter-spacing: 6px;
            word-spacing: 10px;
            margin: 5vh 0;
            text-transform: uppercase;
            user-select: none;
        }
        .informations-section-paragraph {
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            color: rgb(100, 100, 100);
            line-height: 1.5rem;
            letter-spacing: 0.2px;
            word-spacing: 0.35px;
            width: 90%;
        }
    }
    .pink-vase-wrapper {
        margin-top: 10vh;
        background-color: #FBB9A4;
        height: 42.5vh;
        width: 47.5%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        .pink-vase-description {
            height: 100%;
            width: 35%;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            user-select: none;
            .vase-title {
                font-size: 1.025rem;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            }
            .vase-slogan {
                margin-top: 2rem;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                width: 87.5%;
                font-size: 2.5rem;
                color: rgb(40, 40, 40);
                font-weight: 500;
                letter-spacing: 3px;
                word-spacing: 3px;
                text-transform: uppercase;
                user-select: none;
            }
            .vase-button {
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                letter-spacing: 1.25px;
                word-spacing: 2px;
                cursor: pointer;
                padding-bottom: 0.3rem;
                display: inline-block;
                font-weight: 500;
                border-bottom: 1px solid rgba(0, 0, 0, 1);
            }
        }
        .vase-image {
            margin-top: 2.5vh;
            background-image: url('https://imagebank.rosendahl.com/cdn/xZcCGb/Kahler-Omaggio-Nuovo-Vase-Dusty-rose-690163.png?d=6838');
            height: 170%;
            width: 50%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
    .yellow-clock-wrapper {
        margin-top: -5vh;
        background-color: #87b1ff;
        height: 57.5vh;
        width: 42.5%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        position: relative;
        .yellow-clock-description {
            height: 100%;
            width: 50%;
            margin-left: 3rem;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            user-select: none;
            .clock-title {
                font-size: 1.025rem;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            }
            .clock-slogan {
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                width: 87.5%;
                font-size: 2.25rem;
                color: rgb(0, 0, 0);
                font-weight: 500;
                letter-spacing: 3px;
                word-spacing: 3px;
                text-transform: uppercase;
                user-select: none;
            }
            .clock-button {
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                letter-spacing: 1.25px;
                word-spacing: 2px;
                cursor: pointer;
                padding-bottom: 0.3rem;
                display: inline-block;
                font-weight: 500;
                border-bottom: 1px solid rgba(0, 0, 0, 1);
            }
        }
        .clock-image {
            position: absolute;
            width: auto;
            height: 82.5%;
            top: 7.5%;
            right: -17.5%;
        }
    }
    .newsletter {
        width: 47.5%;
        margin-top: 12.5vh;
        border: 1px solid rgb(175, 175, 175);
        padding: 1.5rem 4rem;
        user-select: none;
        h2 {
            font-size: 3.35rem;
            color: rgb(30, 30, 30);
            font-weight: 400;
            letter-spacing: 5px;
            margin: 2.75rem 0;
            text-transform: uppercase;
        }
        .newsletter-info {
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            color: gray;
        }
        label {
            padding-bottom: 0.35rem;
            border-bottom: 1px solid gray;
            input {
                width: 30%;
                font-size: 1rem;
                margin: 2.5rem 0;
                padding-bottom: 0.35rem;
                font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                outline: none;
                border: none;
                border-bottom: 1px solid gray;
                &::placeholder {
                    color: gray;
                }
            }
        }
        button {
            outline: none;
            border: none;
            background-color: transparent;
            text-transform: uppercase;
            letter-spacing: 1.5px;
            font-weight: 500;
            cursor: pointer;
        }
    }
}